html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Montserrat", sans-serif;
  width: 100%;
  min-height: 100vh;
}

a {
  display: block;
  text-decoration: none;
  color: black;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

@media (min-width: 1024px) {
  .contenedor {
    width: 80%;
  }
}
.text-center {
  text-align: center;
}

.postre {
  margin: 3rem 0 2rem 0;
}
.postre__heading {
  font-family: "Playfair Display", serif;
  font-size: 2.8rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .postre__heading {
    font-size: 3.8rem;
  }
}
.postre__box {
  max-width: 50rem;
  padding-left: 2rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .postre__box {
    margin-bottom: 4rem;
  }
}
.postre__descripcion {
  list-style-image: url(../img/6dots.svg);
  font-style: italic;
  font-weight: 500;
  font-size: 1.4rem;
}
.postre__imagen {
  border-radius: 1.4rem;
  overflow: hidden;
}

@media (min-width: 768px) {
  .receta__contenedor {
    display: flex;
    justify-content: space-between;
  }
}
.receta__resumen {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 1.6rem;
}
@media (min-width: 768px) {
  .receta__resumen {
    display: flex;
    flex-direction: column;
    padding: 4rem;
    border-radius: 1.2rem;
    background: #FFF;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  }
}

.resumen {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.resumen:nth-child(1) {
  grid-column: 1/4;
}
.resumen__name, .resumen__tiempo {
  margin: 0;
}
.resumen__name {
  font-size: 0.8rem;
  text-transform: uppercase;
  color: #BDBDBD;
  font-weight: 700;
}
@media (min-width: 768px) {
  .resumen__name {
    font-size: 1rem;
  }
}
.resumen__tiempo {
  font-size: 1rem;
}
.resumen__tiempo--orange {
  color: #F2994A;
}
@media (min-width: 768px) {
  .resumen__tiempo {
    font-size: 1.4rem;
  }
}
.resumen__icono {
  width: 1.7rem;
}
@media (min-width: 768px) {
  .resumen__icono {
    width: 3.4rem;
  }
}